import React, { Component } from 'react';
import styled from 'styled-components';
import { connect } from "react-redux";
import { setCategory} from "../../actions/iconActions";
import { bindActionCreators } from "redux";
import { BREAKPOINT_LG,TRANSITION,BREAKPOINT_MD,GREEN_COLOR,FONT_COLOR,LIGHT_BACKGROUND,BLUE_COLOR,DARK_BORDER_COLOR, DARK_FONT_COLOR, BORDER_COLOR_DARK} from '../types';
import data from '../../reducers/data/categories.json'

const CategoryList = styled.ul`
    padding: 0px;
    list-style: none;
    margin: 0px;
     @media screen and (min-width: ${BREAKPOINT_LG}){
        padding:0px; 
        padding-left:0px;padding-top:0px;

 
        }
  

    li{
        font-size: 14px;
        
        
        padding: 0px;
        cursor: pointer;
        display: inline-block;
        
        width: 100%;
        
        
        @media screen and (min-width: ${BREAKPOINT_LG}){
        width: 100%;
    display:block;}
        div{
        border-radius: 2px;
        padding: 5px 20px;
        border: 2px solid  transparent;
        transition:${TRANSITION};
        &:active {
    background: #f9f9f9;
}
        -webkit-touch-callout: none;
          -webkit-user-select: none;
          -khtml-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
           @media screen and (min-width: ${BREAKPOINT_LG}){
        min-width: 160px;}
            display: block;
            color: ${ FONT_COLOR};
            
           &:hover{
           
            
        } 
        }
        
        &.active{
            
            
            div{
                  color:#b06c4a;
              
            }
            
        }
    }
`;
class Categories extends Component {
constructor(props) {
        super(props);
       this.state= {
categories: data
       }
        this.handleClick = this.handleClick.bind(this);
      

    }
    
    handleClick(id){
        var el = document.querySelector(".icons-right-section");
        var boundingRect = el.getBoundingClientRect();
         window.scroll({
    top: (boundingRect.top+window.pageYOffset)-64,  
    behavior: 'smooth' 
  });
var name="";
 for (var i = 0; i < this.state.categories.length; i++){
   
    if ( this.state.categories[i].id == id){
        name = this.state.categories[i].name
    }
  }
        this.props.setCategory(id,name,this.props.style,this.props.search);
 this.props.changeCategories(false);
    }
render(){
    const categoryItems = this.state.categories.map((category,num) => {

        return (<li key={num} className={this.props.search===category.name ? 'active':''} onClick={()=>this.handleClick(category.id)}><div>{category.name}</div></li>
        );
    });

    return (

        <div className="fadeIn animated" id="filter">
         <CategoryList>
         <li  onClick={()=>this.handleClick(null)}><div>All</div></li>
            {categoryItems}
         </CategoryList>   
        </div>
    );
}
};

function mapDispatchToProps(dispatch) {
    return bindActionCreators(
        {
            setCategory,
        },
        dispatch
    );
}

export default connect(
    null,
    mapDispatchToProps
)(Categories);